.task-preview {
  overflow: hidden;
  background-color: #fff;
  border-radius: em(10px);
  cursor: pointer;
  text-align: start;
  box-shadow: 0 1px 0 rgba(9, 30, 66, 0.251);
  font-size: 14px;
  white-space: normal;
  margin: em(1px) em(4px) em(10px);

  .cover-color {
    height: 32px;
  }

  .cover-img {
    height: 260px;
    background-repeat: no-repeat;

  }

  .task-content {
    padding: em(8px) em(8px) em(4px) em(12px);
    overflow: hidden;

    p {
      margin: 0 0 em(4px);
      word-wrap: break-word;
      margin-bottom: 5px;
    }

    .task-preview-labels {
      display: flex;
      flex-wrap: wrap;
      gap: 4px;
      margin-block-end: 4px;

      .task-preview-label {
        width: em(45px);
        height: em(9px);
        border-radius: 4px;
      }
    }

    .task-preview-members {
      display: flex;
      justify-content: center;
      align-items: center;
      .member-img {
        display: flex;
        justify-content: flex-end;
          img {
            width: 25px;
          }
      }
    }

    .task-icons {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      font-size: 13px;
      color: #626f86;
      margin-block-end: 4px;

     

      & > * {
        margin-inline-end: 10px;
        padding: 2px;
      }

      i {
        color: #626f86;
      }

      .fa-regular {
        margin-inline-end: 6px;
      }

      .done {
        background-color: #1f845a;
        margin: 2px;
        padding: 4px 5px;
        border-radius: 3px;

        span {
          color: #fafbfc;
        }

        i {
          color: #fafbfc;
        }
      }

      .date {
        color: #6b778c;
      }

      .due-sticker,
      .todosDone {
        border-radius: 3px;
        padding: 4px 5px;
        margin-inline-end: 4px;
      }

      .overdue {
        background-color: #f5bbb5;
        margin: 2px;
        padding: 4px 5px;
        border-radius: 3px;

        span, i {
          color: #ae2a19;
        }

        &:hover {
          background-color: #e8766c;
        }
      }

      .soon {
        background-color: #e2b203;
        margin: 2px;
        padding: 4px 5px;
        border-radius: 3px;

        span, i {
          color: $clr-base-5;
        }

        &:hover {
          background-color: #c79d05;
        }
      }
    }
  }
}
